.card_city {
  color: #235e97;
  font-size: 18px;
  border-radius: 20px;
  padding: 20px;
  width: 500px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  flex-wrap: wrap;

  .header {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  .main {
    margin: 30px;
    text-align: center;

    .temp {
      display: flex;
      justify-content: center;
      align-items: flex-start;
      font-weight: 600;
      font-size: 70px;

      .grad {
        font-size: 30px;
      }
    }

    .desc {
      font-size: 19px;
    }
  }

  .info {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;

  }

  .city {
    font-size: 32px;
    font-weight: 500;
  }

  .date {

    font-size: 17px;
  }

}
